<template>
  <div id="login">
    <div class="from_box">
      <div class="from_box_z">
        <div class="from">
          <div class="type_box">
            <div style="color: rgb(8, 38, 88);">登录</div> <span style="left: 8px;"></span>
          </div>
          <div class="input_box">
            <div>账号</div><img src="/static/icon/phone.png"><input type="text" v-model="loginform.phone" placeholder="登录账号">
          </div>
          <div class="input_box">
            <div>密码</div><img src="/static/icon/pass.png"><input type="password" v-model="loginform.password" placeholder="登录密码">
          </div>
          <div class="input_box" v-if="google">
            <div>谷歌验证码</div><img src="/static/icon/pass.png"><input type="password" v-model="loginform.safety_code" placeholder="谷歌验证码">
          </div>
          <div class="denglu_btn" @click="login">登 录</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        loginform:{
          phone:'',
          password:'',
          safety_code:''
        },
        loginactive:false,
        google:false
      }
    },
    methods:{
      login(){
        if(this.loginform.phone == ''){
            this.$Message.warning("请输入账号");
        }else if(this.loginform.password == ''){
            this.$Message.warning("请输入密码");
        }else{
            if(this.google){
              if(this.loginform.safety_code == ''){
                return this.$Message.warning("请输入密码");
              }
            }
            this.$http.get('/api/login/user_login',this.loginform, res => {
                 if (res.status == 200) {
                     if(res.data.code == 200){
                         window.localStorage.setItem('user_token',res.data.data);
                         this.$router.push('/main');
                     }else if(res.data.code == 202){
                         this.google = true;
                     }else{
                       this.$Message.warning(res.data.msg);
                     }
                 }
            });
        }
      }
    }
  }
</script>
<style>
</style>



